@font-face {
  font-family: "ptmono";
  src: url("../fonts/ptmono-regular.woff2"), url("../fonts/ptmono-regular.woff");
  font-style: normal;
  font-weight: 400; }

@font-face {
  font-family: "roboto";
  src: url("../fonts/roboto-regular.woff2"), url("../fonts/roboto-regular.woff");
  font-style: normal;
  font-weight: 400; }

@font-face {
  font-family: "roboto-condenced";
  src: url("../fonts/robotocondensed-regular.woff2"), url("../fonts/robotocondensed-regular.woff");
  font-style: normal;
  font-weight: 400; }

@font-face {
  font-family: "roboto-condenced";
  src: url("../fonts/robotocondensed-bold.woff2"), url("../fonts/robotocondensed-bold.woff");
  font-style: normal;
  font-weight: 700; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-family: "ptmono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  font-style: normal;
  color: #666666;
  background-color: #1b2d37; }

img {
  max-width: 100%;
  height: auto; }

a {
  font-weight: 700;
  text-decoration: none; }

.logo {
  display: flex;
  margin-right: 8.4%; }
  .logo img {
    margin-right: 16px; }
  .logo:hover {
    opacity: 0.6;
    transition: opacity 0.2s ease; }
  .logo:active {
    opacity: 0.3;
    transition: opacity 0.2s ease; }

.logo__name {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  margin: 0;
  margin-top: 5px; }
  .logo__name span:first-child {
    font-family: "roboto-condenced", "Arial", sans-serif;
    font-size: 25px;
    line-height: 29px;
    font-weight: 700;
    color: #ffffff; }
  .logo__name span:last-child {
    font-family: "roboto", "Arial", sans-serif;
    font-size: 12px;
    line-height: 14px;
    font-weight: 400;
    color: #788891; }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 33px 13.18%; }

.header__menu-button {
  position: relative;
  width: 37px;
  height: 13px;
  margin: 0;
  margin-right: auto;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer; }
  .header__menu-button:hover {
    opacity: 0.6;
    transition: opacity 0.2s ease; }
  .header__menu-button:active {
    opacity: 0.3;
    transition: opacity 0.2s ease; }
  .header__menu-button::before, .header__menu-button::after {
    content: "";
    position: absolute;
    width: 37px;
    height: 1px;
    background-color: #ffffff; }
  .header__menu-button::before {
    top: 0;
    left: 0; }
  .header__menu-button::after {
    bottom: 0;
    left: 0; }

.header__callback-button {
  flex-shrink: 0;
  margin-right: 4px;
  padding: 13px 26px;
  font-family: "roboto-condenced", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid #ffffff;
  border-radius: 5px; }
  .header__callback-button:hover {
    opacity: 0.6;
    transition: opacity 0.2s ease; }
  .header__callback-button:active {
    opacity: 0.3;
    transition: opacity 0.2s ease; }

@media (max-width: 1023px) {
  .header {
    padding: 20px 25px; }
  .header__logo {
    margin: 0;
    margin-right: auto; }
  .header__logo-name span:first-child {
    margin-bottom: 2px; }
  .header__menu-button {
    margin: 0;
    margin-top: -5px;
    margin-right: 37px; }
  .header__callback-button {
    display: none; } }

@media (max-width: 767px) {
  .header {
    padding: 17px;
    padding-right: 15px; }
  .header__logo img {
    width: 38px;
    height: 38px; }
  .header__logo-name {
    display: none; }
  .header__menu-button {
    margin-top: -3px;
    margin-right: 27px; } }

.contacts {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 430px;
  margin-right: 3.5%;
  margin-left: 2.5%; }

.contacts__item {
  display: flex;
  align-items: center; }
  .contacts__item:last-child {
    margin-right: 0; }
  .contacts__item svg {
    margin-right: 20px; }
  .contacts__item:last-child svg {
    margin-right: 16px; }
  .contacts__item a {
    display: flex;
    line-height: 18px;
    color: #ffffff; }
    .contacts__item a:hover {
      opacity: 0.6;
      transition: opacity 0.2s ease; }
    .contacts__item a:active {
      opacity: 0.3;
      transition: opacity 0.2s ease; }

@media (max-width: 1023px) {
  .contacts {
    width: auto;
    margin: 0;
    margin-top: -4px; }
  .contacts__item {
    margin-right: 33px; }
    .contacts__item svg {
      width: 24px;
      height: 24px;
      margin: 0; }
    .contacts__item span {
      display: none; }
    .contacts__item:first-child svg {
      width: 25px;
      height: 18px; }
    .contacts__item:last-child svg {
      margin: 0; } }

@media (max-width: 767px) {
  .contacts__item {
    margin-right: 24px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }
